import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ContractApiService } from 'src/app/core/services/contact.services/contract.api.service';
import { JsonService } from 'src/app/core/services/json.services/json.service';
import { TransactionFormComponent } from '../../transaction.form/transaction.form.component';

@Injectable()
export class DynamicFormMainService {
    private session: any;
    constructor(
        private contractApiService: ContractApiService,
        private jsonApiService: JsonService,
        private dialogService: MatDialog
    ) { }
    public getSession(): any {
        return this.session;
    }
    public getSessionOwnerInfo(): any {
        return this.session.ownerInfo;
    }
    public getSessionActions(): any[] {
        return this.session.actions;
    }
    public setSessionActions(actions: any[]): void {
        this.session.actions = actions;
    }
    public getSessionProducers(): any[] {
        return this.session.producers;
    }
    public setSessionProducers(producers: any[]): void {
        this.session.producers = producers;
    }
    public clearSession(): void {
        this.session = {
            ownerInfo: {
                chain: '',
                name: this.contractApiService.getUserName(),
                account: '',
                expiration: '2021-12-03T09:51:48.000',
            },
            actions: [],
            producers: []
        };
        if (this.contractApiService.getUserName() && this.contractApiService.getUserName() !== '') {
            this.addingNewActionToSession();
        }
    }
    private addingNewActionToSessionViaDialog(after: any): void {
        this.dialogService.open(TransactionFormComponent, {
            width: '250px',
        }).afterClosed().subscribe(after);
    }
    public addingNewActionToSession(): void {
        this.addingNewActionToSessionViaDialog((action) => {
            if (!action) {
                return;
            }
            else {
                this.session.actions.push({
                    name: action.name,
                    action: {
                        name: action.action.name,
                        fields: action.action.fields,
                        correctPermissions: [],
                        _fields: action.action._fields
                    },
                    auths: [
                        {
                            _name: '',
                            _perm: '',
                            perms: []
                        }
                    ]
                });
            }
            this.session.ownerInfo.account = this.contractApiService.getUserName();
        });
    }
    public async importSessionFromJsonFile(): Promise<void> {
        this.jsonApiService.importObjectFromJson((e) => {
            const reader = new FileReader();
            reader.onload = (e2 => {
                const obj = JSON.parse(e2.target.result.toString());
                if (this.session.accInfo.chain != obj.t.accInfo.chain) {
                    alert(`First you need to change the chain to ${obj.t.accInfo.chain}`);
                }
                else if (this.session.accInfo.chain != obj.t.accInfo.chain) {
                    alert(`First you need to log in as ${obj.t.accInfo.name}`);
                }
                else {
                    this.session = obj.session;
                }
            });
            reader.readAsText(e.target.files[0]);
        });
    }
    public exportSessionOnJsonFile(): void {
        this.jsonApiService.exportObjectAsJson(
            {
                session: this.session
            }
            , 'msig-save-' + new Date().toLocaleString());
    }
    //TODO: test version
    public proposeSessionActions(): void {
        this.contractApiService.deploy(this.session);
    }
}